.App {
    text-align: center;
}
  
  .logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-top: 30px;
    width: 20%;
  } 

.news-intro-col {
    display: -webkit-inline-flex;
    display: inline-flex;
    color: black;
    height: 100%;
  }
  
  .news-intro-panel {
    margin: auto 5px;
    text-align: left;
  }
  
  .news-description {
    text-align: left;
  }
  
  .news-description p {
    font-size: 18px;
  }
  
  .news-chip {
    font-size: 18px;
  }
  
  .fill {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      overflow: hidden;
  }
  
  .fill img {
      padding-left: 20px;
      padding-right: 20px;
      -webkit-flex-shrink: 0;
              flex-shrink: 0;
      min-width: 100%;
      max-height: 250px;
      object-fit: cover;
  }
  
